:import('Styles/palette.css') {
  i__const_successAccent_106: successAccent;
  i__const_successNeutral_107: successNeutral;
  i__const_successLight_108: successLight;
  i__const_infoAccent_109: infoAccent;
  i__const_infoNeutral_110: infoNeutral;
  i__const_infoLight_111: infoLight;
  i__const_warningAccent_112: warningAccent;
  i__const_warningNeutral_113: warningNeutral;
  i__const_warningLight_114: warningLight;
  i__const_dangerAccent_115: dangerAccent;
  i__const_dangerNeutral_116: dangerNeutral;
  i__const_dangerLight_117: dangerLight;
  i__const_neutralColorMedium_118: neutralColorMedium;
  i__const_neutralColorLight_119: neutralColorLight;
}


:export {
  successAccent: i__const_successAccent_106;
  successNeutral: i__const_successNeutral_107;
  successLight: i__const_successLight_108;
  infoAccent: i__const_infoAccent_109;
  infoNeutral: i__const_infoNeutral_110;
  infoLight: i__const_infoLight_111;
  warningAccent: i__const_warningAccent_112;
  warningNeutral: i__const_warningNeutral_113;
  warningLight: i__const_warningLight_114;
  dangerAccent: i__const_dangerAccent_115;
  dangerNeutral: i__const_dangerNeutral_116;
  dangerLight: i__const_dangerLight_117;
  neutralColorMedium: i__const_neutralColorMedium_118;
  neutralColorLight: i__const_neutralColorLight_119;
}


.notification {
  padding: 20px 20px 5px 20px;
  background-color: i__const_neutralColorLight_119;
  border: 1px solid;
  border-color: i__const_neutralColorMedium_118;
  border-radius: 2px;
}


.footer{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
}

.action{
  padding: 5px;
  cursor: pointer;
}

.success{
  color: i__const_successAccent_106;
  background-color: i__const_successNeutral_107;
  border-color: i__const_successLight_108;
}

.info{
  color: i__const_infoAccent_109;
  background-color: i__const_infoNeutral_110;
  border-color: i__const_infoLight_111;
}

.warning{
  color: i__const_warningAccent_112;
  background-color: i__const_warningNeutral_113;
  border-color: i__const_warningLight_114;
}

.danger{
  color: i__const_dangerAccent_115;
  background-color: i__const_dangerNeutral_116;
  border-color: i__const_dangerLight_117;
}


