:import('Styles/palette.css') {
  i__const_darkColor_83: darkColor;
  i__const_secondaryColor_84: secondaryColor;
  i__const_highlightColor_85: highlightColor;
  i__const_neutralColorMedium_86: neutralColorMedium;
  i__const_whiteColor_87: whiteColor;
  i__const_pinkRed_88: pinkRed;
}

:export {
  colors: 'Styles/palette.css';
  darkColor: i__const_darkColor_83;
  secondaryColor: i__const_secondaryColor_84;
  highlightColor: i__const_highlightColor_85;
  neutralColorMedium: i__const_neutralColorMedium_86;
  whiteColor: i__const_whiteColor_87;
  pinkRed: i__const_pinkRed_88;
}

.title, .subtitle, .header, .p {
  margin: 0;
}

.p {
  color: i__const_darkColor_83;
}

.label {
  opacity: 0.5;
  font-size: 14px;
  margin-bottom: 2px;
}

.highlight {
  color: i__const_highlightColor_85;
}

.secondary {
  color: i__const_secondaryColor_84;
}

.disabled {
  color: i__const_neutralColorMedium_86;
  cursor: not-allowed;
}

.inverted {
  color: i__const_whiteColor_87;
}

.smallText {
  font-size: 13px;
}

.lead {
  font-size: 18px;
}

.normalText {
  font-weight: normal;
  font-family: 'Lato-Regular';
}

.normalFont {
  font-weight: normal;
  font-family: 'FaktPro-Normal';
}

.bold {
  font-weight: bold;
  font-family: 'FaktPro-Bold';
}

.light {
  font-weight: 300;
  font-family: 'FaktPro-Thin';
}

.error {
  color: i__const_pinkRed_88;
}

a {
  font-family: 'FaktPro-Normal';
  text-decoration: none;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'FaktPro-Normal';
}



