:import('Styles/common.css') {
  i__const_cardShadow_102: cardShadow;
}

:import('Styles/palette.css') {
  i__const_neutralColorLight_103: neutralColorLight;
  i__const_primaryColor_104: primaryColor;
  i__const_neutralColorMedium_105: neutralColorMedium;
}

:export {
  colors: 'Styles/palette.css';
  cardShadow: i__const_cardShadow_102;
  neutralColorLight: i__const_neutralColorLight_103;
  primaryColor: i__const_primaryColor_104;
  neutralColorMedium: i__const_neutralColorMedium_105;
}

.dropdown {
  display: block;
  position: relative;
}

.container {
  position: absolute;
  display: block;
  min-width: 50px;
  min-height: 10px;
  background-color: i__const_neutralColorLight_103;
  padding: 0;
  margin: 0;
  border-radius: 2px;
  overflow: hidden;
  -webkit-box-shadow: i__const_cardShadow_102;
          box-shadow: i__const_cardShadow_102;
  -webkit-animation-name: fadeInDown;
          animation-name: fadeInDown;
  -webkit-animation-duration: 200ms;
          animation-duration: 200ms;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
}

.dropdownTrigger {
  cursor: pointer;
}

@-webkit-keyframes fadeInDown {
  from {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
    opacity: 0.1;
  }

  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}

@keyframes fadeInDown {
  from {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
    opacity: 0.1;
  }

  to {
    -webkit-transform: translateY(0);
            transform: translateY(0);
    opacity: 1;
  }
}




